import React from 'react';
import logo from "../static/dojo_logo.png";
import Cube from '../components/Cube';
const About = () => {
    return (
        <>
            <div id="about" className="about__top">
                <div>
                   <h1>About Me</h1>
                   <p>
                   At the coding dojo, I worked through three full stacks of web development. Algorithm challenges started on day 1, and continued until graduation. I first learned basic uses of for-loops, and by the end of my schooling I was inverting Singly-Linked-Lists and building Binary trees. I began by learning HTML/CSS and basic DOM manipulation with JS. I then moved on to the Python/Django web framework with mySQL. I learned how to construct models and create relationships between documents in a database. After passing 5-hour timed exam with flying colors, I moved on to the MERN stack. I learned about many of React.js's great features including: managing state with react hooks, making API calls with the axios node module, learning how to utilize the power of JS promises and async/await functions, and developing Single-Page-Applications. In combination with React.js, I learned how to build and use an Express.js server to create an API using mongoose queries, to interface with a mongoDB database. Lastly I learned to develop complex applications using the Java/Spring MVC, with the power of Spring Web annotations and using .jsp files to render data from the controller. This stack taught me a lot about strongly-typed, object-oriented languages similar to C#. 
                   </p><br />
                   
                </div>
                <div className="about__top__flex">
                    <img width="" src={logo} /> 
                    <p className="about__bold">
                       At the Coding Dojo, I set myself apart by passing all three stacks with the Dojo's "Black Belt" requirements. This is a grade that shows that the individual went beyond the curriculum, by researching the framework and it's documentation, to learn and implement new tools for development. 
                   </p> 
                </div>
                
                
            </div>
            <div className="about__bottom">
                <div className="cube__container">
                   <Cube /> 
                </div>
                <p>
                    Since I was young, I've always been infatuated with computers and all they could do. But I wasn't content with just <i>using</i> computers, i had to make them my own.<br />
                    I was always an individual who wanted to take something great, and make it better.<br />
                    I later learned how to build computers and gained a greater understanding of how each component works. I went on to dabble in code in high school, and I'm now persuing my dream as a Software Developer.
                    
                </p>
                
            </div>
        </>
    )
}

export default About
    