import React from 'react'
import { FaPython, FaReact } from "react-icons/fa";
import { SiDjango, SiGooglemaps, SiMongodb, SiMysql, SiRedux } from "react-icons/si";
import { GrStripe } from "react-icons/gr";
import ImageGallery from "react-image-gallery";
import img1 from "../static/img-driversblog/Dashboard.PNG";
import img2 from "../static/img-driversblog/Meet.PNG";
import img3 from "../static/img-driversblog/MeetsPage.PNG";
import img4 from "../static/img-driversblog/Profile Page.PNG";
import Capture1 from "../static/img-ecom/Capture1.PNG";
import Capture2 from "../static/img-ecom/Capture2.PNG";
import Capture3 from "../static/img-ecom/Capture3.PNG";
import Capture4 from "../static/img-ecom/Capture4.PNG";
const Projects = () => {
    const dbImages = [
        {
            original: img1
        },
        {
            original: img2
        },
        {
            original: img3
        },
        {
            original: img4
        },
    ]
    const ecomImages = [
        {
            original: Capture1
        },
        {
            original: Capture2
        },
        {
            original: Capture3
        },
        {
            original: Capture4
        },
    ]


    return (
            
        <div className="portfolio">
            <h1 id="portfolio__header">Portfolio</h1>
            <div className="projects__container">
                <div className="project">
                    <div className="gallery__container">
                        <ImageGallery items={dbImages} />
                    </div>
                    
                    <span className="project__links">
                        <a href="http://54.157.224.161/" rel="noreferrer" target="_blank">View Live</a>
                        <a href="https://github.com/AdotHamilton/DriversBlog" rel="noreferrer" target="_blank">View Code</a>
                    </span>
                    <div className="project__text">
                        <h1>DriversBlog</h1> 
                    
                        <p>This project is built on the Python-Django framework. DriversBlog includes login and registration, and
                    leverages back-end validations for database integrity, as well as integrating BCrypt for password hashing
                    and user security. DriversBlog allows users to create discussion board threads to talk about their favorite
                    cars, or get wisdom from other users on how to maintain their vehicle. Users can follow other users, like
                    posts, and create ads for car-meets. The meets feature integrates the Google Maps Embed API to display an
                    interactive map for the meeting location; meets nationwide can be searched by state.</p>
                    <h2>Technologies Used</h2>
                    <div className="project__icons">
                        
                        <FaPython size={42} />
                        <SiDjango size={42} />
                        <SiMysql size={42} />
                        <SiGooglemaps size={42} />
                        

                    </div>
                </div>
                    
                    
                </div>
                <div className="project">
                    <div className="gallery__container">
                        <ImageGallery items={ecomImages} />
                    </div>
                    
                    <span className="project__links">
                        <a href="http://44.195.66.102/" rel="noreferrer" target="_blank">View Live</a>
                        <a href="https://github.com/AdotHamilton/EcomDotCom" rel="noreferrer" target="_blank">View Code</a>
                    </span>
                    <div className="project__text">
                        <h1>Ecom.com</h1>
                        
                        <p>
                        Ecom.com is an Ecommerce website built on the React front-end framework, with an Express back-end,
                        and a MongoDB database. Ecom.com utilizes React hooks such as useEffect and useState, coupled with the
                        axios node module to request page data from the back-end. User’s cart data is persisted using React/Redux
                        and is saved in local storage to persist cart items even after signing out. Transactions are handled securely by
                        the Stripe API/node module. This project uses functional login and registration with mongoose queries, and
                        leverages the Bcrypt node module for password security. This project has been designed with mobile-use in
                        mind and is styled reactively, using CSS Grid layout and “@media” queries to change the page layout to fit
                        the browsing device
                        </p>
                        <h2>Technologies Used</h2>
                        <div className="project__icons">
                            <FaReact size={42} />
                            <SiRedux size={42} />
                            <GrStripe size={42} />
                            <SiMongodb size={42} />
                        </div>
                    </div>
                    
                </div>
            </div>

        </div>
    )
}

export default Projects
