import React from 'react'
import { Link } from "react-scroll";
import { IconContext } from "react-icons";
import { AiTwotoneMail } from "react-icons/ai"
import { FaGithub, FaLinkedinIn  } from "react-icons/fa";
import portrait from "../static/portrait.jpg";
const Nav = () => {
    
    const menuHandler = () => {
        var x = document.getElementById("hamburger");
        x.classList.toggle("change");
        var nav = document.getElementsByClassName("nav-hidden");
        if(nav[0]){
            nav[0].className = "nav-active"
        } else {
            document.getElementsByClassName("nav-active")[0].className = "nav-hidden";
        }
    }

    
    return (
        
        <div className="nav">
            <div id="hamburger" onMouseOver={menuHandler}>
                <div class="bar1"></div>
                <div class="bar2"></div>
                <div class="bar3"></div>
            </div>
            <ul className="nav-hidden" onMouseLeave={menuHandler}>
                
                <li className="nav__item"><Link to="home" spy={true} smooth={true}>Home</Link></li>
                <li className="nav__item"><Link to="about" spy={true} smooth={true}>About Me</Link></li>
                <li className="nav__item"><Link to="portfolio" spy={true} smooth={true}>Projects</Link></li>
                <li className="nav__item"><Link to="contact" spy={true} smooth={true}>Contact Me</Link></li>
                <li className="nav__bottom"><img src={portrait} className="image-round"/>
                    Austin Hamilton<br />
                    <span className="icon-container">
                        <IconContext.Provider value={{color: 'white'}}>
                            <a href="https://github.com/AdotHamilton" rel="noreferrer" target="_blank"><FaGithub /></a>
                            <a href="https://www.linkedin.com/in/austin-hamilton-434741209/" rel="noreferrer" target="_blank"><FaLinkedinIn /></a>
                            <a href="mailto:austin.hamilton.j@gmail.com" rel="noreferrer" target="_blank"><AiTwotoneMail /></a>
                        </IconContext.Provider>

                        
                    </span>
                </li>
                
            </ul>
            
        </div>
    )
}

export default Nav
